.searchPage__logo{
    object-fit:contain;
    height:50px;
    margin-left:50px;
    margin-right:50px;
}

.searchPage__header{
    display:flex;
    position:sticky;
    margin-top:40px;
    background-color:white;
    z-index: 100;
    align-items:flex-start;
    border-bottom:1px solid lightgrey;
    padding-top:0;
    padding-bottom:0;
}



.searchPage__headerBody > .search > .search__input{
       margin-top:0px; 
        width:unset;
        margin:unset;
        max-width: unset;
}

.searchPage__options{
    display: flex;
    align-items:center;
    color:grey;
}

.searchPage__options a,
.searchPage__optionsRight a{
    text-decoration:none;
    color:grey;
}

.searchPage__optionsRight,
.searchPage__optionsLeft{
    display:flex;
    justify-content: space-between;
}

.searchPage__optionsRight {
    margin-left:80px !important;
}



.searchPage__option {
    display:flex;
    align-items:center;
    justify-content: space-around;
    margin-right: 20px;   
} 


.searchPage__result {
    margin: 40px 0;
}

.searchPage__resultCount{
    color: #70757a;
    font-size:14px;
}

.searchPage__results{
    width:100%,
}

.searchPage__results{
    max-width:650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom:100px;
}

.searchPage__resultTitle{
    margin:0px;
    padding:0px;
    text-decoration: none;
}

.searchPage__resultSnippet{
    margin:10px;
    padding:0;
}


.seachPage__resultImage{
    object-fit:contain;
    height: 20px;
    width:20px;
    margin-right:10px;

}


.searchPage__options2{
    display:none;
}

@media (max-width:520px){

    .searchPage2{
        margin-left:15px;
    }
    .searchPage__options2{
        display: flex;
        align-items:center;
        margin-left:10px;
       
        color:grey;
    }
    .searchPage__logo{
        object-fit:contain;
        height:50px;
        display: block;
        flex-direction: row;
        margin-left:20px;
        margin-right:20px;
    }
    
    .searchPage__header{
        display:flex;
        position:sticky;
        top:0 !important;
        background-color:white;
        z-index: 100;
        width:unset;
        flex-direction: column;
        flex:wrap;
        align-items:center;
        border-bottom:1px solid lightgrey;
        padding-top:10px;
        margin-bottom:20px;
        padding-bottom:15px;
    }
    
    
    
    .searchPage__headerBody > .search > .search__input{
           margin-top:0px; 
            width:unset;
            margin:unset;
            max-width: unset;
            margin-top:20px;
    }
    
    .searchPage__options{
        display: none;
        
    }
    
    .searchPage__options a,
    .searchPage__optionsRight a{
        text-decoration:none;
        color:grey;
    }
    
    .searchPage__optionsRight,
    .searchPage__optionsLeft{
        display:flex;
        justify-content: space-between;
    }
    
    .searchPage__optionsRight {
        width:100%;
        margin-right:0 !important;
        margin-left: 0 !important;
    }
    
    
    
    .searchPage__options2 {
        display:flex;
        flex:1;
        align-items:stretch;
        justify-content: space-around;
        margin-right: 0;
           
    } 
    

    .searchPage__option{
        display:flex;
        justify-content: space-evenly;
    }
    
    .searchPage__result {
        margin: 10px 0;
    }
    
    .searchPage__resultCount{
        color: #70757a;
        font-size:14px;
    }
    
    .searchPage__results{
        width:unset;
        margin-left:0;
        margin-right:5px;
    }
    
    .searchPage__resultTitle{
        margin:0px;
        padding:0px;
        text-decoration: none;
    }
    
    .searchPage__resultSnippet{
        margin:10px;
        padding:0;
    }
    
    
    .seachPage__resultImage{
        object-fit:contain;
        height: 20px;
        width:20px;
        margin-right:10px;
    
    }
}